<template>
    <div class="keyboard-wrapper">
        <div class="close-button">
            <span class="closer" @click="close">×</span>
        </div>
        <div class="working-block">
            <v-text-field
                :value="valueInternal"
                :label="inputName"
                outlined dense clearable
                class="input"
                @input="onInput"
                @keyup.enter.native="onSubmit"
            ></v-text-field>
            <div class="keyboard specific-keys">
                <v-btn
                    v-if="buttons"
                    v-for="letter of buttons"
                    :key="letter"
                    depressed rounded
                    @click="handleKeyboard(letter)"
                >{{ letter }}</v-btn>
            </div>
            <div class="keyboard common-keys">
                <v-btn
                    v-if="commonButtons"
                    v-for="letter of commonButtons"
                    :key="letter"
                    depressed rounded large dark
                    color="teal"
                    @click="handleKeyboard(letter)"
                >{{ letter }}</v-btn>
            </div>

            <div class="submit-wrapper">
                <v-btn
                    depressed
                    text
                    class="mr-5"
                    @click="close"
                >Cancel</v-btn>
                <v-btn
                    depressed
                    color="primary"
                    @click="onSubmit"
                >Submit</v-btn>
            </div>
        </div>

    </div>
</template>

<script>
import handleKeyboardMixin from '@/mixins/handleKeyboardMixin';

export default {
    name: "Keyboard",
    props: ['buttons', 'value', 'inputName'],
    mixins: [handleKeyboardMixin],
    data() {
        return {
            inputElement: null,
            valueInternal: '',
            commonButtons: Object.freeze(['⬅', 'space', 'del', 'backspace', '➡']),
        }
    },
    methods: {
        close() {
            this.$emit('close');
        },
        onInput(value) {
            this.valueInternal = value;
        },
        onSubmit() {
            this.$emit('submit', this.valueInternal);
            this.close();
        }
    },
    watch: {
        value: {
            immediate: true,
            handler(val) {
                this.valueInternal = val;
            }
        }
    },
    async mounted() {
        await this.$nextTick();
        this.inputElement = document.querySelector('.keyboard-wrapper .v-text-field input');
        this.inputElement.focus();
    },
}
</script>

