    margin-top: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    & > * {
        flex: 0 0 10%;
        margin: 3px;
    }
}
.specific-keys {
    ::v-deep .v-btn {
        text-transform: none;
    }
}
.common-keys {
    margin-top: 40px;
    & > * {
        margin: 10px;
    }
    ::v-deep .v-btn {
        text-transform: capitalize;
