function handleButtonValue({ buttonValue, selectionStart, selectionEnd, existingValue }) {
    let buttonValueMod;
    let selectionStartMod;
    let selectionEndMod;
    if (buttonValue === "backspace"){
        if ((selectionStart > 0) && (selectionStart === selectionEnd)) {
            selectionStartMod = selectionStart - 1;
        }
        buttonValueMod = "";
    } else if (buttonValue === "del"){
        if ((selectionStart < existingValue.length) && (selectionStart === selectionEnd)) {
            selectionEndMod = selectionEnd + 1;
        }
        buttonValueMod = "";
    } else if (buttonValue === "space"){
        buttonValueMod = " ";
    } else if (buttonValue === '⬅') {
        if (selectionStart < selectionEnd) {
            selectionEndMod = selectionStart;
        } else {
            selectionStartMod = Math.max(0, selectionStart - 1);
            selectionEndMod = selectionStartMod;
        }
        buttonValueMod = '';
    } else if (buttonValue === '➡') {
        if (selectionStart < selectionEnd) {
            selectionStartMod = selectionEnd;
        } else {
            selectionStartMod = Math.min(existingValue.length, selectionStart + 1);
            selectionEndMod = selectionStartMod;
        }
        buttonValueMod = '';
    } else {
        buttonValueMod = buttonValue;
        selectionStartMod = selectionStart;
        selectionEndMod = selectionEnd;
    }

    return { buttonValueMod, selectionStartMod, selectionEndMod };
}


export default {
    methods: {
        handleKeyboard(buttonValue) {
            if (!this.inputElement) {
                return;
            }
            const input = this.inputElement;

            let { selectionStart, selectionEnd } = input
            const existingValue = this.valueInternal;

            const { buttonValueMod, selectionStartMod, selectionEndMod } =
                handleButtonValue({ buttonValue, selectionStart, selectionEnd, existingValue });

            input.value = existingValue.slice(0, selectionStartMod)
                               + buttonValueMod
                               + existingValue.slice(selectionEndMod, existingValue.length);
            this.valueInternal = input.value;
            input.focus();
            input.selectionStart = selectionStartMod + buttonValueMod.length;
            input.selectionEnd = selectionStartMod + buttonValueMod.length;
        },
    }
}
